import React, { useState } from "react";
import { Search as SearchIcon, Bell } from "lucide-react";

const Search = ({ onSearch }) => {
  const [searchQuery, setSearchQuery] = useState("");

  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      onSearch(searchQuery);
    }
  };

  return (
    <div className="w-full bg-white fixed top-0 left-0 z-50">
      <div className="flex items-center justify-between border-b pb-3 px-6">

        <h1 className="text-xl font-semibold text-black">
          Reported Content
        </h1>

        <div className="flex items-center gap-6">
          <div className="relative w-80">
            <SearchIcon className="absolute left-4 top-2.5 h-5 w-5 text-gray-500" />

            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyDown={handleKeyDown}
              placeholder="Search By User ID"
              className="w-full py-2 pl-12 pr-4 rounded-full border border-gray-300 shadow-sm bg-white focus:outline-none focus:ring-2 focus:ring-gray-200"
            />
          </div>

          <div className="relative cursor-pointer">
            <Bell className="h-7 w-7 text-yellow-500" />
            <span className="absolute -top-1 -right-1 h-3 w-3 bg-red-500 rounded-full border-2 border-white"></span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Search;
