import React, { useState } from "react";
import Search from "@/src/component/reportedcontent/Search";
import ReportedContent from "@/src/component/reportedcontent/ReportedContent"; 

const Index = () => {
  const [searchText, setSearchText] = useState("");

  return (
    <>
      {/* TOP SEARCH BAR */}
      <Search onSearch={(value) => setSearchText(value)} />

      {/* CONTENT LIST */}
      <div className="pt-[90px]">
        <ReportedContent search={searchText} />
      </div>
    </>
  );
};

export default Index;
